$default-text-font: 'Yantramanav', sans-serif;
$default-heading-font: 'Yantramanav', sans-serif;

$first-main-color: #ff5e14;
$second-main-color: #00235a;
$third-blue-color: #0a1229;
$fourth-blue-color: #09316f;
$default-heading-color: #001659;
$default-text-color: #565969;
$third-main-color: #e0e1e3;
$alternate-text-color: #12192d;
$alternate-paragraph-color: #cfd3e5;
$footer-text-color: #9ca5af;

$default-background-color: #fff;
$additional-background-color: #f6f6f6;
$default-border-color: #e8e8e9;
$dark-border-color: #1d263f;
$default-border-radius: 2px;
$default-box-shadow: 0 0 4.85px 0.15px rgba(#000, 0.09);
$first-hover-box-shadow: 0px 10px 20px 0px transparentize($first-main-color, 0.5);
$second-hover-box-shadow: 0px 10px 20px 0px transparentize($second-main-color, 0.5);
$third-hover-box-shadow: 0 10px 20px 0 rgba(0,16,42,.5);
$huge-box-shadow: 0 14px 65px 0 rgba(122,131,153,.5);

$aux-hover-box-shadow: 0px 10px 20px 0px rgba(23, 41, 75, 1);
$fourth-hover-box-shadow: 0px 10px 20px 0px rgba(18, 25, 145, 0.5);

$first-main-transition: .1s ease-in-out;

$header-light-color: #fff;
$header-light-hover-color: $first-main-color;
$header-dark-color: $default-heading-color;
$header-dark-hover-color: $first-main-color;

// input elements
$input-height: 50px;
$sselect-input-height: $input-height;
$input-vertical-padding: 12px;
$input-horizontal-padding: 16px;
$input-margin: 25px;

